import { useState } from 'react';
import './App.css';
import jejumap from './jejumap.jpg';

function Start(){
  return(
    <div className="gotrip" onClick={ e => {
      e.target.classList.add("active");
      document.querySelector(".daybuttons").classList.add("active");
        }
      }
    >여행시작!</div>
  )
}

function openNav(){
  document.querySelector(".container.active").style.marginLeft = "50vw";
  document.querySelector(".leftnavi").style.width = "50vw";
  document.querySelector(".container.active").style.backgroundColor = "rgba(0,0,0,0.4)";
}

function closeNav() {
  document.querySelector(".leftnavi").style.width = "0";
  document.querySelector(".container.active").style.marginLeft = "0";
  document.querySelector(".container.active").style.backgroundColor = "";
}


function Container(props){
  return(
  <div className="container">
    <TopNavigation navi={["첫째날","둘째날","셋째날"]}></TopNavigation>
    {props.children}
  </div>
  )
}

function Selectday(props){ 
  const change = props.changeday
  const containerActive = (mode) => {
    document.querySelector(".container").classList.add("active");
    change(mode);
  };
  return(
    <div className="daybuttons">
    <div className="btn firstday" onClick={(e)=>{containerActive("first");e.target.parentNode.classList.remove("active");}}>첫째날</div>
    <div className="btn secondday" onClick={(e)=>{containerActive("second");e.target.parentNode.classList.remove("active");}}>둘째날</div>
    <div className="btn thirdday" onClick={(e)=>{containerActive("third");e.target.parentNode.classList.remove("active");}}>셋째날</div>
    </div>
  )
}

function LeftNavigation(props){
  const navi = props.navi
  return(
    <nav className="leftnavi">
    {navi.map(array => <a key={array} href="#">{array}</a>)}
    </nav>
  )
} 

function TopNavigation(props){
  const navi = props.navi
  let [navimode, setmode] = useState("closed")
  return(
    <div className="topNaviContainer">
    <div className="navibutton" onClick={(e)=>{
      if(navimode==="closed"){
        openNav();
        e.target.style.backgroundImage = "url('https://image.flaticon.com/icons/png/512/561/561189.png')"
        setmode("open")
      } else if(navimode==="open"){
        closeNav();
        e.target.style.backgroundImage = "url('https://image.flaticon.com/icons/png/512/660/660376.png')"
        setmode("closed")
      }}}></div>
    <nav className="topnavi">
      <ul>
        {navi.map(array => <li key={array}>{array}</li>)}
      </ul>
    </nav>
    </div>
  )
} 


function Mapview(){
  return(
    <div className="Map">
      <img src={jejumap} alt="제주도 지도"></img>
    </div>
  )
}

function Detail(){
  return(
    <ul className="detail">
      <li>hihi</li>
      <li>hihi</li>
      <li>hihi</li>
      <li>hihi</li>
    </ul>
  )
}

function Todolist(props){
  const list = props.list
  return(
    <ul>
     {list.map((list)=><li key={list}>{list}</li>)}
    </ul>
  )
}



function App() {
  const [mode, setmode] = useState("first")
  const changeday = (mode) => setmode(mode)
  return (
    <div className="App">
      <Start></Start>
      <Selectday changeday={changeday} mode={mode}></Selectday>
      <LeftNavigation navi={["첫째날","둘째날","셋째날"]}></LeftNavigation>
      <Container>
        <Mapview></Mapview>
        <Detail></Detail>
        <Todolist list={["무지개해안도로", "도두봉", "한라 수목원", "삼성혈", "바이제주", "이호테우해변","샤론앤이든"]}></Todolist>
      </Container> 
    </div>
  );
}

export default App;
